footer{
  background-color: #000;
  position: relative;
  padding: 30px 0px;
  display: flex;
  justify-content: center;
  font-family: 'Noto Sans TC', sans-serif;
  z-index: 99;
}
footer a{
  text-decoration: none;
}
footer h4{
  margin: 0;
  line-height: 27px;
}
.footer-container{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
}
footer .top{
  position: absolute;
  top: 1px;
  left: 0;
  transform: translateY(-100%);
}
.footer-left{
  display: flex;
  flex-direction: column;
  padding: 0px 20px;
}
.footer-left .copyright{
  color: #ABFF1D;
  font-size: 12px;
}
.left-bottom{
  flex: 1;
}
.footer-brand{
  display: flex;
  flex: 1;
  margin-bottom: 5px;
}
.footer-brand img{
  height: 23px;
  margin-right: 7px;
}
.footer-brand .brand-name h4{
  color: #ABFF1D;
  display: flex;
  flex-direction: column;
  font-size: 12px;
  font-weight: 400;
  line-height: 12px;
}
.footer-center{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.footer-center ul{
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}
.footer-center .center-top li{
  margin: 0px 10px;
}
.footer-center .center-top a{
  font-weight: 500;
  color: #fff;
  letter-spacing: 1px;
  font-size: 16px;
  text-align: center;
  transition-duration: .5s;
  text-decoration: none;
}
.footer-center .center-top a:hover{
  color:#ABFF1D;
}
.divider{
  border-top: solid 2px #fff;
  width: 100%;
  margin: 15px 0px;
}
.footer-center .center-bottom{
  font-weight: 500;
  color: #fff;
  letter-spacing: 1px;
  font-size: 14px;
  text-align: center;
  transition-duration: .5s;
}
.footer-center .center-bottom li{
  margin: 0px 10px;
  display: flex;
  align-items: center;
}
.footer-center .center-bottom svg{
  height: 13px;
}
.footer-right{
  width: 185px;
}
footer .rectangle1{
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-70%);
  height: 80px;
}
footer .rectangle2{
  position: absolute;
  right: 10%;
  height: 70px;
  bottom: 0;
}

@media (max-width: 1100px) {
  .footer-container{
    width: 90%;
    flex-direction: column;
    align-items: flex-start;
  }
  .footer-left{
    margin-bottom: 30px;
    padding: 0px 10px;
  }
  .footer-center{
    align-items: flex-start;
  }
}
@media (max-width: 940px) {
  footer{
    padding: 50px 0px;
  }
  .footer-container{
    max-width: 380px;
  }
  .footer-left{
    padding: 0px;
  }
  .footer-center .center-top ul{
    flex-wrap: wrap;
    width: 100%;
  }
  .footer-center .center-top ul li{
    width: 50%;
    margin: 0px;
    margin-bottom: 10px;
  }
  .footer-center .center-bottom ul{
    flex-direction: column;
    align-items: flex-start;
  }
  .footer-center .center-bottom ul li{
    margin: 0px 0px 10px 0px;
  }
  .footer-right{
    height: 0px;
  }
}